.project-Wrapper {
  color: white;
  display: grid;
  justify-content: center;
  text-align: center;
  user-select: none;
  margin-top: 28px;
  margin-bottom: 28px;
}
.project-name {
  font-family: "Russo one", sans-serif;
  font-size: 1.9rem;
  width: 100%;
  background-color: var(--maroon);
  border-radius: 5px 5px 0px 0px;

  /* background-color: #ff4500; */
}
.project-image {
  width: 350px;
}
.project-image img {
  display: block;
  width: 100%;
  height: 400px;
  object-fit: cover;
}
.project-play-button {
  width: 100%;
  background-color: var(--blue);
  border-radius: 0px 0px 5px 5px;
}
.project-play-button button {
  font-size: 1.2rem;
  font-family: "Russo one", sans-serif;
  padding: 4.5px 25px;
  cursor: pointer;
  user-select: none;
}
